:root {
  --box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Alata";
  background-color: #eef2f7;
}

.main-container {
  text-align: center;
  background-color: transparent;
  border-radius: 5px;
  color: black;
  padding: 20px;
  width: 600px;
}

h1 {
  font-size: 48px;
  margin-bottom: 40px;
}

.session-group {
  font-size: 32px;
  margin: 10px 20px;
  display: flex;
  justify-content: space-between;
}
.btn {
  font-family: "Alata";
  border: none;
  background-color: transparent;
  font-size: 32px;
  padding: 10px;
  margin: inherit 5px;
  color: black;
  border-radius: 0px;
}
.btn:focus {
  outline: none;
}
.btn:hover {
  cursor: pointer;
}
.btn:disabled {
  border-right: 5px solid grey;
  border-left: 5px solid grey;
}
.btn:disabled:hover {
  cursor: not-allowed;
}
.btn-inc {
  border-right: 5px solid #31ff80;
  border-left: 5px solid #31ff80;
  /* background-color: white; */
}

.btn-dec {
  border-right: 5px solid #fa432f;
  border-left: 5px solid #fa432f;
  /* background-color: white; */
}

.timer {
  box-shadow: var(--box-shadow);
  background-color: white;
  font-size: 72px;
  font-weight: 800;
  margin: 20px 0;
}
.timer span {
  display: inline-block;
  width: 120px;
  padding: 10px;
}
.timer-control .btn {
  width: 120px;
  margin-right: 30px;
}
#root > div > div:nth-child(2) > span:nth-child(1) > span {
  width: 300px;
  display: flex;
  justify-content: space-between;
}
#root > div > div:nth-child(2) > span:nth-child(2) > span {
  width: 300px;
  display: flex;
  justify-content: space-between;
}
